import { Injectable } from '@angular/core';
import { HttpClient, HttpResponse } from '@angular/common/http';
import { RX_SERVER_LOGS } from './server-logs.constant';
import { Observable } from 'rxjs';
import { words } from 'lodash';
import { ILogFileNames, IServerLogsConfig } from './server-logs.types';
import { saveAs } from 'file-saver';

@Injectable({
  providedIn: 'root'
})
export class RxServerLogsService {
  constructor(private httpClient: HttpClient) {}

  getConfig(): Observable<IServerLogsConfig> {
    return this.httpClient.get<IServerLogsConfig>(RX_SERVER_LOGS.configUrl);
  }

  save(logsConfig: IServerLogsConfig): Observable<any> {
    return this.httpClient.post(RX_SERVER_LOGS.configUrl, logsConfig);
  }

  downloadServerLogs(logFileNames: ILogFileNames): Observable<any> {
    return this.httpClient.post(RX_SERVER_LOGS.downloadUrl, logFileNames, {
      observe: 'response',
      responseType: 'arraybuffer'
    });
  }
}
